<template>
  <div>
    <el-container style="height: 700px; border: 2px solid #eee">
      <el-header style="height: 230px; font-size: 40px">
        <el-card
          shadow="hover"
          style="
            background-image: url(https://picx.zhimg.com/v2-39c50c73f90a5731986831f3cb483bd3_1440w.jpg?source=32738c0c);
          "
        >
          InfoPerson
          <div class="block" style="text-align: left">
            <br />
            <el-avatar
              shape="square"
              :size="100"
              src="https://cube.elemecdn.com/9/c2/f0ee8a3c7c9638a54940382568c9dpng.png"
            >
            </el-avatar>
          </div>
        </el-card>
      </el-header>

      <el-main>
        <div>
          <!-- 这是用户基本信息部分 -->
          <el-skeleton :loading="loading" animated>
            <template slot="template">
              <div style="padding: 14px">
                <el-skeleton :rows="6" animated />
              </div>
            </template>
            <div>
              <h1>基本信息</h1>
              <el-card shadow="hover">
                <div style="padding-left: 14px; padding-right: 90px">
                  <el-row
                    :gutter="20"
                    style="
                      margin-bottom: 20px;
                      &:last-child {
                        margin-bottom: 0;
                      }
                    "
                  >
                    <div style="border-radius: 4px">
                      <el-col :span="6">
                        <b>用户昵称</b>
                      </el-col>
                      <el-col :span="6">
                        <b>用户ID</b>
                      </el-col>
                      <el-col :span="6">
                        <b>性别</b>
                      </el-col>
                      <el-col :span="6">
                        <b>出生日期</b>
                      </el-col>
                    </div>
                  </el-row>
                  <el-row
                    :gutter="20"
                    style="
                      margin-bottom: 20px;
                      &:last-child {
                        margin-bottom: 0;
                      }
                    "
                  >
                    <div style="border-radius: 4px">
                      <el-col :span="6">
                        <div id="Nickname">
                          <el-input
                            v-model="nickname"
                            placeholder="请输入昵称"
                          ></el-input>
                        </div>
                      </el-col>
                      <el-col :span="6">
                        <div>username</div>
                      </el-col>
                      <el-col :span="6">
                        <div id="gender">
                          <el-radio v-model="gender" label="1">男</el-radio>
                          <el-radio v-model="gender" label="2">女</el-radio>
                        </div>
                      </el-col>
                      <el-col :span="6">
                        <div id="birthday">
                          <el-date-picker
                            v-model="birthday"
                            type="date"
                            placeholder="选择日期"
                          >
                          </el-date-picker>
                        </div>
                      </el-col>
                    </div>
                  </el-row>
                </div>
              </el-card>
              <br />
              <el-card shadow="hover">
                <h1>收藏算法</h1>
                <el-table :data="algorithm" style="width: 100%">
                  <el-table-column prop="collect_id" label="算法">
                  </el-table-column>
                  <el-table-column
                    fixed="right"
                    label="操作"
                    width="100"
                    type="index"
                    :index="del"
                  >
                    <template>
                      <el-button @click="look_algorithm" type="text" size="small"
                        >查看</el-button
                      >
                    </template>
                  </el-table-column>
                </el-table>
              </el-card>
              <el-card shadow="hover">
                <h1>收藏文章</h1>
                <el-table :data="message" style="width: 100%">
                  <el-table-column prop="collect_id" label="文章id">
                  </el-table-column>
                  <el-table-column
                    fixed="right"
                    label="操作"
                    width="100"
                    type="index"
                    :index="del"
                  >
                    <template>
                      <el-button @click="look_message" type="text" size="small"
                        >查看</el-button
                      >
                    </template>
                  </el-table-column>
                </el-table>
              </el-card>
            </div>
          </el-skeleton>
        </div>
        <br />
      </el-main>
    </el-container>
  </div>
</template>

<script>
import main from "D:/EdgeDownload/vueGUI/vueproject/src/main.js";
const axios = require("axios");

export default {
  data: () => ({
    loading: true,
    timerId: null,
    nickname: "jocker",
    birthday: "2024-06-28",
    gender: "1",
    algorithm: [],
    message: [],
    index: "",
  }),
  methods: {
    handleClick(row) {
      console.log(row);
    },
    look_algorithm() {
      //跳转到指定文章页面
      this.$router.push("algorithm");
    },
    look_message(){
        this.$router.push("forum");
    }
  },
  mounted() {
    if (!main.user.username || !main.user.password) {
      this.$message.error("你还没有登录账号，禁止访问其他页面！！！");
      this.$router.push("/log");
    }
    // 设置计时器
    else {
      this.timerId = setTimeout(() => {
        // 这里可以放置计时结束后要执行的代码
        this.loading = false;
      }, 1500); // 3000毫秒后执行
      //获取收藏模型
      axios
        .post(
          main.url + "/collect/get_info",
          {
            username: main.user.username,
            collect_type: "算法模型",
          },
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
            },
            emulateJSON: true,
          }
        )
        .then((res) => {
          if (res.data) {
            for (var i = 0; i < res.data.length; i++) {
              var x = {};
              x.collect_id = res.data[i].collect_id;
              this.algorithm.push(x);
            }
          }
        });
      //获取收藏文章
      axios
        .post(
          main.url + "/collect/get_info",
          {
            username: main.user.username,
            collect_type: "论坛文章",
          },
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
            },
            emulateJSON: true,
          }
        )
        .then((res) => {
          console.log(res.data);
          if (res.data) {
            for (var i = 0; i < res.data.length; i++) {
              var x = {};
              x.collect_id = res.data[i].collect_id;
              this.message.push(x);
            }
          }
        });
    }
  },
};
</script>

<style scoped></style>